import React from 'react'
import Layout from '../components/Layout'
import photo from '../Data/image.jpg'

const Pro = () => {
    return (
        <Layout>
            <div style={{backgroundImage: `url(${photo})`}} className='h-100 w-100'>
                <h3 className='text-center'>Profile Of User Define The Identity Of Patients</h3>
            </div>
        </Layout>
    )
}

export default Pro
